.output-component {
    display: flex;
    flex-direction: column;
    gap: 36px 0;
    border: 1px solid black;
    background-color: azure;

    .output-img {
        margin-top: 12px;
    }
}

.down-button {
    border: 1px solid black;
    display: inline-flex;
    gap: 10px;
    padding: 5px 10px;
    border-radius: 5px;
    margin-top: 10px;
    cursor: pointer;

    button {
        border: none;
        background-color: transparent;
        cursor: pointer;
    }
}

.list-image {
    margin: 20px;
}
.result-text {
    float: left;
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}